
  import { defineComponent, onMounted, ref } from 'vue';
  
  export default defineComponent({
    setup() {
      const loadingText = ref<HTMLElement | null>(null);
      let currentIndex = 0;
      let direction = 1;
  
      function animateLoadingText(): void {
        if (!loadingText.value) return;
  
        const text: string = loadingText.value.textContent || '';
        const newText: string = text
          .split('')
          .map((char: string, index: number) => {
            if (index === currentIndex) {
              return direction === 1 ? char.toUpperCase() : char.toLowerCase();
            }
            return char;
          })
          .join('');
  
        loadingText.value.textContent = newText;
  
        if (currentIndex === text.length - 1 && direction === 1) {
          direction = -1;
        } else if (currentIndex === 0 && direction === -1) {
          direction = 1;
        } else {
          currentIndex += direction;
        }
  
        setTimeout(animateLoadingText, 50);
      }
  
      onMounted(() => {
        animateLoadingText();
      });
  
      return { loadingText, animateLoadingText };
    },
  });
  