
import LoadingAnimateVue from '@/components/LoadingAnimate.vue';
import { defineComponent, onMounted, ref } from 'vue';

export default defineComponent({
    methods: {
        waitForElement: function (selector: string, callback: (element: Element)=> void) {
            const checkElement = setInterval(() => {
                const element = document.querySelector(selector)

                if (element) {
                    clearInterval(checkElement)
                    callback(element)
                }
            }, 100)
        },
        updateIsView: function (value: boolean) {
            this.$emit('loading-contoller', value)
            console.log("execcute")
        }    
    },
    components: {
        LoadingAnimateVue
    },
    props: {
        isView: Boolean
    },
    mounted() {
        this.waitForElement("#twitter-widget-0", (element) => {
            console.log("twitter widget loaded", element)
            const iframe = element
            iframe.addEventListener('load', () => {
                    console.log("iframe loaded")
                    this.updateIsView(false)
            })
        })
        
        setTimeout(() => {
            let tweetScript = document.createElement('script')
            tweetScript.setAttribute('src', 'https://platform.twitter.com/widgets.js')
            tweetScript.setAttribute('async', 'true')
            document.head.appendChild(tweetScript)
        }, 100)
    }
})
